<template>
	<div id="app">
		<keep-alive>
		 	<transition name="fade">
				<router-view></router-view>
			</transition>
		</keep-alive>
		<Foot v-show="hideTab"></Foot>
	</div>
</template>
<script>
	import Foot from './components/Foot.vue'
	import {mapGetters,mapActions} from 'vuex'
		export default{
			computed:mapGetters([
				'hideTab'
				]),
			watch:{
				$route(to,from){
					if(to.path=='/home/limit/details'){//判断商品详情的抢购图标出现
						this.$store.dispatch('showDetails');
					}
					if(to.path !='/home' && to.path !='/cart' && to.path !='/category' && to.path !='/user'){
						this.$store.dispatch('hideTable');//隐藏底部tab
					}else{
						this.$store.dispatch('showTable');//显示底部tab
					}
				}
			},
			methods:{
				goBack(){
					window.history.go(-1);
				}
			},
			components:{
				Foot,
			},
	}
</script>
<style>
	.icon{
	    width: 30px;
	    height: 30px;
	}
	.icon::before{
		content: "";
	    position: absolute;
	    width: 12px;
	    height: 12px;
	    border: 1px solid #79c34b;
	    border-width: 1px 0 0 1px;
	    -webkit-transform: rotate(315deg);
	    transform: rotate(315deg);
	    top: 13px;
	    left: 13px;
	}
	
	.pleft5{
		left: 5px;
	}
	.pleft7{
		left: 7px;
	}
	.fade-enter-active{
	  	transition: opacity .5s
	}
	.fade-enter{
	  	opacity: 0
	}
</style>


